@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700;800&family=Roboto:wght@100&display=swap);

*,
*:after,
*:before {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
html,
body {
  height: 100%;
}
.container {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100vh;
  padding: 3.3vw 3.3vw 0;
  top: 0;
  left: 0;
  overflow-x: hidden;
  z-index: 2;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}
.ibg {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.ibg img {
  display: none;
}
.page {
  position: relative;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  display: flex;
  flex-direction: column;
}

.main_bg{
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top:0;
}

a{
  text-decoration: none;
}

::-webkit-scrollbar {
  width: 0;
}

